}


@media screen and (max-width: 300px) {
    nav {
        bottom: 2rem;
        margin: 0px -12px;
    }
   
    
}

.about_container{
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap: 15%;
    gap: 15%;
}

.about_me {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
        45deg,
        transparent,
        var(--color-primary),
        transparent
    );
    display: grid;
    place-items: center;
}

.about_img {
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}

.about_img:hover{
    transform: rotate(0);
}

.about_cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.about_card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
}

.about_card:hover{
    background: transparent;
    border-color: var(--color-bg-variant);
    cursor: default;
}

.about_icon {
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about_card h5{
    font-size: 0.95rem;
}

.about_card small{
    font-size: 0.8rem;
    color: var(--color-light);
}

.about_content p{
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}



/* ********************* MEDIA QUERIES*********************** */
@media screen and (max-width: 1024px) {
    .about {
        text-align: center;
    }
    .about_container {
        grid-template-columns: 1fr;
        grid-gap: 0;
        gap: 0;
    }

    .about_me{
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about_content p{
        margin: 1rem 0 1.5rem;
    }
  }


@media screen and (max-width: 600px) {
    .about_me {
        width: 65%;
        margin: 0 auto 3rem;
    }

    .about_cards{
        grid-template-columns: 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }

    .about_content{
        text-align: center;
    }

    .about_content p{
        margin: 1.5rem 0;
    }
  }

@media screen and (max-width: 300px) {
    .about{
        width: 390px;
    }
  }



.experience__container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
}

.experience__container > div {
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.experience__container > div:hover {
    background: transparent;
    border-color: var(--color-bg-variant);
    cursor: default;
}

.experience__container > div h3 {
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}

.experience__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 2rem;
    row-gap: 2rem;
}

.experience__details {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}

.experience__details-icon{
    margin-top: 6px;
    color: var(--color-primary);

}



/* ********************* MEDIA QUERIES*********************** */
@media screen and (max-width: 1024px) {
    .experience__container {
        grid-template-columns: 1fr;
    }

    .experience__container > div {
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }

    .experience__content{
        padding: 1rem;
    }

}


@media screen and (max-width: 600px) {

    .experience__container {
        grid-gap: 2rem;
        gap: 2rem;
    }

    .experience__container > div {
        width: 100%;
        padding: 2rem 1rem;
    }


}
.service_container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 3rem;
    gap: 3rem;
}

.service {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    background: var(--color-bg-variant);
    border-radius: 0 0 2rem 2rem;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.service:hover{
    background: transparent;
    border-color: 1px solid var(--color-primary-variant);
    cursor: default;
}

.service_head {
    background: var(--color-primary);
    padding: 2re,;
    border-radius: 0 0 2rem 2rem;
    box-shadow: 0 2rem 1rem rgb(0, 0, 0, 0.1);
}

.service_head h3 {
    color: var(--color-bg);
    font-size: 1.5rem;
    text-align: center;
}

.service_list {
    padding: 2rem;
}

.service_list li {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 0.8rem;
}

.service_list-icon {
    color: var(--color-primary);
    margin-top: 2rem;
}

.service_list p {
    font-size: 0.9rem;
}



/* ********************* MEDIA QUERIES*********************** */
@media screen and (max-width: 1024px) {

    .service_container{
        grid-template-columns: 1fr 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }
    .service {
        height: auto;
    }
  }


@media screen and (max-width: 600px) {
    .service_container{
        grid-template-columns: 1fr;
        grid-gap: 1.5rem;
        gap: 1.5rem;
    }
  }
  
@media screen and (max-width: 300px) {
    #services{
        width: 390px;
    }
  }
.portfolio_container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2.5rem;
    gap: 2.5rem;
}

.portfolio_items {
    background: var(--color-bg-variant);
    padding: 1.3rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.portfolio_items:hover {
    background: transparent;
    border: 1px solid var(--color-primary-variant);
}

.portfolio_item-img {
    border-radius: 1.5rem;
    overflow: hidden;
}

.portfolio_items h3{
    margin: 1.2rem 0 2rem;
    text-align: center;
}

.portfolio_item-cta {
    display: flex;
    justify-content: center;
    grid-gap: 1.5rem;
    gap: 1.5rem;
    margin-bottom: 1rem;
}



/* ********************* MEDIA QUERIES*********************** */
@media screen and (max-width: 1024px) {

    .portfolio_container {
        grid-template-columns: 1fr 1fr;
        grid-gap: 1.2rem;
        gap: 1.2rem;
        width: 80%;
    }

}

@media screen and (max-width: 600px) {
    .portfolio_container {
        grid-template-columns: 1fr;
        grid-gap: 1rem;
        gap: 1rem;
        width: auto;
    }

}

@media screen and (max-width: 300px) {
    #portfolio{
        width: 390px;
    }
  }
